.form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 600px;
}
.label {
  display: flex;
  flex-direction: column;
  margin-bottom: 4px;
  color: #373737;
  font-size: 20px;
  font-weight: 700;
}

.input {
  background: rgb(227, 227, 227);
  border-radius: 10px;
  border: 2px solid #474747;
  color: #191919;
  font-size: 19px;
  height: 20px;
  margin: 10px 0 10px 0px;
  width: 400px;
  padding: 15px;
}

.btn {
  background: rgb(227, 227, 227);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1) inset;
  border-radius: 5px;
  border: 1px solid #cfd5d9;
  padding: 15px 0;
  width: 434px;
  font-size: 20px;
  font-weight: 900;
  transition: all 1s;
}

.btn:hover {
  transform: scale(1.05);
}
